<template>
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">
        <i class="fas fa-chart-pie mr-1"></i>
        {{ $t("tiposResoluciones") }}
      </h3>
    </div>
    <!-- /.card-header -->
    <div class="card-body">
      <div class="chart">
        <DoughnutChart
          style="position: relative; height: 300px;"
          :chart-data="datacollection"
        ></DoughnutChart>
      </div>
    </div>
    <!-- /.card-body -->
  </div>
</template>

<script>
import DoughnutChart from "@/components/graficos/DoughnutChart.js";

import { chartColors, dynamicColors } from "@/util/graficos";
import { mapState, mapGetters } from "vuex";

function backgroundColor(label) {
  let color;
  switch (label.split(" ")[0]) {
    case "faltas":
      color = chartColors.faltas;
      break;

    case "penal":
      color = chartColors.penal;
      break;

    case "contravencional":
      color = chartColors.contravencional;
      break;

    case "amparo":
      color = chartColors.green;
      break;

    case "allanamiento_autonomo":
      color = chartColors.orange;
      break;

    case "habeas_corpus":
      color = chartColors.grey;
      break;

    default:
      color = dynamicColors();
      break;
  }

  return color;
}

export default {
  components: {
    DoughnutChart,
  },
  data() {
    return {
      datacollection: {},
    };
  },
  mounted() {
    this.fillData();
  },
  computed: {
    ...mapState(["resolucionActual"]),
    ...mapGetters(["getTiposLabel", "getTiposValues"]),
  },
  watch: {
    resolucionActual() {
      this.fillData();
    },
    ["$i18n.locale"]() {
      this.fillData();
    },
  },
  methods: {
    fillData() {
      this.datacollection = {
        labels: this.getTiposLabel?.map(
          (x, index) =>
            `${this.$i18n.t("tipos." + x)} (${this.getTiposValues[index]})`
        ),
        datasets: [
          {
            data: this.getTiposValues,
            backgroundColor: this.getTiposLabel?.map((x) => backgroundColor(x)),
            //["red", chartColors.orange, chartColors.yellow]
          },
        ],
      };
    },
  },
};
</script>

<i18n>
{
  "es": {
    "tiposResoluciones" : "Tipos de Resoluciones",
    "tipos": {
      "allanamiento_autonomo": "Allanamiento autonomo",
      "amparo": "Amparo",
      "contravencional": "Contravencional",
      "ejecucion_de_multa": "Ejecucion de multa",
      "faltas": "Faltas",
      "habeas_corpus": "Habeas Corpus",
      "penal": "Penal"
    }
  },
  "en": {
    "tiposResoluciones" : "Topic of Decisions",
    "tipos": {
      "allanamiento_autonomo": "Adminstrative Warrant",
      "amparo": "Constitutional Action",
      "contravencional": "Contraventions",
      "ejecucion_de_multa": "Fine Execution",
      "faltas": "Misdemeanours",
      "habeas_corpus": "Habeas Corpus",
      "penal": "Criminal cases"
    }
  }
}
</i18n>
