export const getRandom = () => Math.floor(Math.random() * 100) + 1;

export const getRandomArray = (cant) => [...Array(cant)].map(() => getRandom());

export const chartColors = {
  red: "#dc3545",
  orange: "#fd7e14",
  yellow: "#ffc107",
  green: "#28a745",
  blue: "#007bff",
  purple: "#6f42c1",
  grey: "#6c757d",
  // Custom
  contravencional: "#ae1c63",
  penal: "#1d6fb8",
  faltas: "#5a792b",
  genero: "#7f338c",
};

export const dynamicColors = function() {
  var r = Math.floor(Math.random() * 255);
  var g = Math.floor(Math.random() * 255);
  var b = Math.floor(Math.random() * 255);
  return "rgb(" + r + "," + g + "," + b + ")";
};
