<template>
  <div class="card">
    <div class="card-header">
      <h3 class="card-title">
        <!-- <i class="fas fa-chart-pie mr-1"></i> -->
        <i class="fas fa-chart-area mr-1"></i>
        {{ $t("resolucionesPorMes") }}
      </h3>
      <div class="card-tools mt-2 mt-sm-0">
        <ul class="nav nav-pills ml-auto">
          <li class="nav-item">
            <a
              class="nav-link active"
              href="#resoluciones-mes-1-semestre"
              data-toggle="tab"
            >
              {{ $t("primerSemestre") }}</a
            >
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              href="#resoluciones-mes-2-semestre"
              data-toggle="tab"
              >{{ $t("segundoSemestre") }}</a
            >
          </li>
        </ul>
      </div>
    </div>
    <!-- /.card-header -->
    <div class="card-body">
      <div class="tab-content p-0">
        <div class="chart tab-pane active" id="resoluciones-mes-1-semestre">
          <line-chart
            style="position: relative; height: 300px;"
            :chart-data="datacollection1"
          ></line-chart>
        </div>
        <div class="chart tab-pane" id="resoluciones-mes-2-semestre">
          <line-chart
            style="position: relative; height: 300px;"
            :chart-data="datacollection2"
          ></line-chart>
        </div>
      </div>
      <!-- <div class="chart">
        <line-chart style="position: relative; height: 300px;" :chart-data="datacollection"></line-chart>
      </div>-->
    </div>
    <!-- /.card-body -->
  </div>
</template>

<script>
import LineChart from "@/components/graficos/LineChart.js";
//import { getRandomArray } from "@/util/graficos";
import { mapState, mapGetters } from "vuex";

export default {
  components: {
    LineChart,
  },
  data() {
    return {
      datacollection1: {},
      datacollection2: {},
    };
  },
  mounted() {
    this.fillData();
  },
  computed: {
    ...mapState(["resolucionActual"]),
    ...mapGetters(["getResolucionesMes"]),
  },
  watch: {
    resolucionActual() {
      this.fillData();
    },
    ["$i18n.locale"]() {
      this.fillData();
    },
  },
  methods: {
    fillData() {
      this.datacollection1 = {
        labels: Object.values(this.$i18n.t("meses.primerSemestre")),
        datasets: [
          {
            label: this.$i18n.t("resoluciones"),
            backgroundColor: "rgba(60,141,188,0.9)",
            borderColor: "rgba(60,141,188,0.8)",
            pointRadius: false,
            pointColor: "#3b8bba",
            pointStrokeColor: "rgba(60,141,188,1)",
            pointHighlightFill: "#fff",
            pointHighlightStroke: "rgba(60,141,188,1)",
            data: this.getResolucionesMes(1 /* Semestre*/),
          },
        ],
      };

      this.datacollection2 = {
        labels: Object.values(this.$i18n.t("meses.segundoSemestre")),
        datasets: [
          {
            label: this.$i18n.t("resoluciones"),
            backgroundColor: "rgba(60,141,188,0.9)",
            borderColor: "rgba(60,141,188,0.8)",
            pointRadius: false,
            pointColor: "#3b8bba",
            pointStrokeColor: "rgba(60,141,188,1)",
            pointHighlightFill: "#fff",
            pointHighlightStroke: "rgba(60,141,188,1)",
            data: this.getResolucionesMes(2 /* Semestre*/),
          },
        ],
      };
    },
  },
};
</script>

<style></style>

<i18n>
{
  "es": {
    "meses": {
      "primerSemestre" : ["Enero", "Febrero", "Marzo", "April", "Mayo", "Junio"],
      "segundoSemestre" :  ["Julio",  "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"]
    },
    "resolucionesPorMes" : "Resoluciones por Mes",
    "primerSemestre" :"1 Semestre",
    "segundoSemestre": "2 Semestre",
    "resoluciones": "Resoluciones"
  },
  "en": {
    "meses": {
      "primerSemestre" : ["January", "February","March","April","May", "June"],
      "segundoSemestre" : ["July","August","September","October","November", "December"]
    } ,
    "resolucionesPorMes" : "Decisions per month",
    "primerSemestre" :"1 Semester",
    "segundoSemestre": "2 Semester",
    "resoluciones" : "Rulings Issued"
  }
}
</i18n>
